import { useEffect, useState } from 'react'
import { LiteMarket } from 'common/api/market-types'
import { api } from 'stonks/lib/api/api'

export function StonksList() {
  const [stonks, setStonks] = useState<LiteMarket[]>([])
  const [error, setError] = useState<string>()

  useEffect(() => {
    const fetchStonks = async () => {
      try {
        const data = await api('search-markets', {
          contractType: 'STONK',
          sort: 'prob-descending',
          limit: 100,
        })
        setStonks(data)
      } catch (e) {
        setError('Failed to load stonks')
        console.error(e)
      }
    }
    fetchStonks()
  }, [])

  if (error) return <div>Error: {error}</div>

  return (
    <div>
      <ul>
        {stonks.map((stonk) => (
          <li key={stonk.id}>
            {stonk.question}: <StonkPrice contract={stonk} />
          </li>
        ))}
      </ul>
    </div>
  )
}
